import axios from 'axios';

let request = axios.create();

request.defaults.baseURL = 'http://localhost:8088';
request.defaults.headers.post['Content-Type'] =
    'application/x-www-form-urlencoded';
// 请求超时
request.defaults.timeout = 3000;

// 响应拦截器
request.interceptors.response.use(
    (res) => {
        return {
            status: res.status,
            message: res.statusText,
            data: res.data,
        };
    },
    (err) => {
        return {
            status:
                err.response | err.toJSON().status
                    ? err.response.status | err.toJSON().code
                    : 500,
            message: err.message,
            requestUrl: err.config.url,
        };
    }
);

// Get请求
export function get(url, params, timeout = 3000) {
    return request.get(url, {
        params,
        timeout,
    });
}

// Post请求
export function post(url, params, timeout = 3000) {
    return request.post(url, {
        data: params,
        timeout,
    });
}

// 文件上传(支持多文件)
export function upload(url, file, name, timeout = 3000) {
    file = !(file instanceof Array) ? Array.from(file) : file;
    let formData = new FormData();
    file.forEach((f) => formData.append(name, file));
    return request.post(url, {
        data: formData,
        headers: {
            'Content-Type': 'multipart/form-data',
        },
        timeout,
    });
}

// 自定义请求
export function build(
    url = null,
    method = 'get',
    baseUrl = null,
    headers = {},
    params = {},
    timeout = 3000,
    auth = {},
    responseType = 'json',
    proxy = {}
) {
    return request({
        url,
        method: method,
        baseUrl,
        headers,
        params,
        data: params,
        timeout,
        auth,
        responseType,
        proxy,
    });
}
