<template>
    <div page-index>
        <div v-shadow="{ radius: 8, width: '80%' }">
            <p>代理</p>
            <p>
                <button
                    type="button"
                    @click="getData">
                    获取服务器数据
                </button>
                &ensp;
                <input
                    type="text"
                    placeholder="当然, 你也可以键入搜索关键字"
                    v-model="keyword" />
                &ensp;
                <span
                    style="color: red"
                    v-show="errMsg"
                    >「{{ errMsg }}」</span
                >
            </p>
            <div style="display: flex; flex-wrap: wrap">
                <p
                    v-for="item in dataList"
                    :key="item.id">
                    <img :src="item.link" />
                </p>
            </div>
        </div>
        <div v-card>
            <p>关于代理</p>
            <div>
                <pre>
解决请求跨域的问题
在vue.config.js中配置如下
例1:
  module.exports = defineConfig({
    devServer: {
        // 代理地址
        proxy: 'http://localhost:8088'
    }
  })
例2:
  module.exports = defineConfig({
    devServer: {
        proxy: {
            '/api': {
                target: 'http://localhost:8088',
                pathRewrite: {
                    '^/api': '/'
                },
                changeOrigin: true
            }
        }
    }
  })
                </pre>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import { build } from '../request';

export default {
    name: 'Proxy',
    data() {
        return {
            dataList: Array(),
            keyword: null,
            errMsg: null,
        };
    },
    methods: {
        // ?name=${this.keyword ? this.keyword : '小乔'}
        getData() {
            axios
                .get(`/api/pic`, {
                    params: {
                        name: this.keyword ? this.keyword : '小乔',
                    },
                })
                .then((res) => {
                    this.dataList = res.data;
                    this.errMsg = null;
                })
                .catch((err) => {
                    this.errMsg = err.code + '#' + err.message;
                    console.log(err.toJSON().status, err.code, err.message);
                });
        },
    },
};
</script>

<style scoped>
img {
    width: 400px;
    margin-right: 1rem;
}
</style>
